/* Header */


.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;

  grid-template-areas:
    'h h'
    'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 3fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(208, 234, 122);

}
/*Dialogs*/

.content {
  grid-area: c;
  background-color: rgb(208, 234, 122);
}